.logoAdmin{
    margin: 0px auto;
    padding: 20px 0px;
    max-width: 100px;
}

.logoAdmin img{width:100%}

.adminWrap{
    height: 100vh;
    background-color: #333 !important;
}

.menuWrap{
    align-items: flex-start;
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding: 0px 23px;
    font-size: 14px;
    color: #fff;
}
.menuWrap a{
    cursor: pointer;
}
.menuWrap span{
    vertical-align: middle;
    padding: 0px 10px;
}